import React from 'react'
import{TripContext} from "../TripsContainer"
import {translate} from "../../../utils/translate"
import styles from "./trips.module.css"
import { Paper } from '@material-ui/core'
import Avatar from '@material-ui/core/Avatar';
import profile from "../../../assets/FACEBOOK_LINE-01-512.png"
import config from "../../../config/backendURLS"
// import AnnouncementIcon from '@material-ui/icons/Announcement';
import TodayIcon from '@material-ui/icons/Today';
import RoomIcon from '@material-ui/icons/Room';
import NavigationOutlinedIcon from '@material-ui/icons/NavigationOutlined';
import CancelIcon from '@material-ui/icons/Cancel';
import TripTimeLine from "./TripTimeline.js";
import TripMap from "./tripMap";

const TripDetails = (props) => {
    
    return (
        <TripContext.Consumer>
            {({state})=>(
                <>
               <Paper elevation={2} square>
               <div style={{padding:"0 3px"}}>
                        <h3 style={{margin:0}}> :</h3>
                </div>
                   <div className={styles.tripContainer}>
                        <div className={styles.part1}>
                        {state.trip&&state.trip.summary&&state.trip.summary.id?<div className={styles.driver}>
                            <div className={styles.tripNumber}>
                                <i className="fas fa-hashtag"></i>
                            </div>
                                <label style={{margin:"0 9px"}} className={styles.driverLabel}>{translate("THE_NUMBER")}:</label>
                              
                                    <p style={{margin:0}}>{state.trip.summary.id}</p>
                            </div>:null} 
                           {state.trip&&state.trip.summary&&state.trip.summary.driver?<div className={styles.driver}>
                           <Avatar src={state.trip.driver.profile_image?`http://api.ride-int.com/${state.trip.driver.profile_image}`:profile} style={{width:25,height:25}}/>
                                <label className={styles.driverLabel}>{translate("DRIVER")}:</label>
                              
                                    <p style={{margin:0}}>{state.trip.summary.driver.full_name}</p>
                            </div>:null} 
                            {state.trip&&state.trip.summary&&state.trip.summary.rider?<div className={styles.driver}>
                            <Avatar src={state.trip.rider.profile_image?`${config.hostDomain}${state.trip.rider.profile_image}`:profile} style={{width:25,height:25}}/>
                                <label className={styles.driverLabel}>{translate("RIDER")}:</label>
                                    <p style={{margin:0}}>{state.trip.summary.rider.full_name}</p>
                            </div>:null} 
                           
                        </div>
                        <div className={styles.part2}>
                        {state.trip&&state.trip.summary&&state.trip.summary.date?<div className={styles.driver}>
                            <TodayIcon style={{color:"#317e9e"}}/>
                                <label className={styles.driverLabel}>{translate("DATE")}:</label>
                                    <p style={{margin:0}}>{state.trip.summary.date}</p>
                            </div>:null} 
                            {state.trip&&state.trip.summary&&state.trip.summary.pickup_location?<div className={styles.driver}>
                                <NavigationOutlinedIcon color="error"/>
                                <label className={styles.driverLabel}>{translate("PICK_UP")}:</label>
                                    <a 
                                    href={state.trip.summary.pickup_location_url}  
                                    style={{margin:0,textDecoration:"none",color:"black"}}
                                    target="_blank" rel="noopener noreferrer">{state.trip.summary.pickup_location}</a>
                            </div>:null} 
                            {state.trip&&state.trip.summary&&state.trip.summary.drop_location?<div className={styles.driver}>
                            <RoomIcon style={{color:"green"}}/>
                                <label className={styles.driverLabel}>{translate("DROP_LOCATION")}:</label>
                                    <a 
                                    href={state.trip.summary.drop_location_url} 
                                    style={{margin:0,textDecoration:"none",color:"black"}}
                                    target="_blank" rel="noopener noreferrer">{state.trip.summary.drop_location}</a>
                            </div>:null}
                            {state.trip&&state.trip.summary&&state.trip.summary.cancel_reason?<div className={styles.driver}>
                               <CancelIcon/>
                                <label className={styles.driverLabel}>{translate("CANCEL_REASON")}:</label>
                                    <p style={{margin:0,textDecoration:"none"}}>{state.trip.summary.cancel_reason}</p>
                            </div>:null}
                        </div>
                   </div>

               </Paper>
               <Paper square elevation={2} style={{margin:"30px 0",overflow:"hidden"}}>
                   <div style={{padding:"0 3px"}}>
                        <h3 style={{margin:0}}>{translate("TIMELINE")}:</h3>
                   </div>
                   <div style={{minHeight:100,display:"flex"}}>
                       <TripTimeLine steps={state.trip.time_line}/>
                   </div>
               </Paper>

               <Paper square elevation={2} style={{overflow:"hidden"}}>
                   <TripMap path={state.trip.way_points}/>
               </Paper>
               </>
            )}
        </TripContext.Consumer>
        
    )
}



export default TripDetails
